.preloader-mini{height:0;width:100%;max-width:700px;position:fixed;bottom:25px;z-index:2}.preloader-mini:before{content:'';height:3px;width:20px;position:absolute;left:0;background-color:var(--preloader-mini);-webkit-animation:preloaderAnim 3s infinite linear;animation:preloaderAnim 3s infinite linear}@media screen and (max-width: 740px){.preloader-mini{max-width:100%;left:0%}}@-webkit-keyframes preloaderAnim{0%{left:0px}100%{left:calc(100% - 20px)}}@keyframes preloaderAnim{0%{left:0px}100%{left:calc(100% - 20px)}}

.books-list{padding:0 10px 0 10px;width:100%;max-width:720px}.books-list__container{display:grid;grid-template-columns:repeat(auto-fill, 100px);grid-gap:1rem;justify-content:space-between;margin:10px 0 0 0}@media screen and (max-width: 730px){.books-list__container{justify-content:space-evenly;margin:10px 6px 0 0}}@media screen and (max-width: 364px){.books-list__container{margin:10px 15px 0 10px}}@media screen and (max-width: 270px){.books-list__container{justify-items:center;justify-content:center;grid-gap:1.2rem}}

.books-list-info{height:25px;width:100%;max-width:700px;background-color:var(--body);box-shadow:0px -9px 13px -10px rgba(141,141,141,0.8);-webkit-box-shadow:0px -9px 13px -10px rgba(141,141,141,0.8);-moz-box-shadow:0px -9px 13px -10px rgba(141,141,141,0.8);position:fixed;display:flex;align-items:center;justify-content:center;flex-wrap:nowrap;bottom:0;z-index:1}.books-list-info__title{font-size:small;color:#9b9b9b}@media screen and (max-width: 740px){.books-list-info{max-width:100%;left:0}}

:root{--body: #fafafa;--scroll: #b6b6b6;--text: #666;--preloader: #eee;--preloader-mini: #fec432;--header: #fafafa;--theme-button-border: #444444;--theme-button-border-hover: #4e4e4e;--input__box: #f1f1f1;--input__box-border-top: var(--header);--input__box-border-bottom: var(--input__box);--input__box-border-focus: #bebebe;--input__control: #888888;--input__control_color: #9c9c9c;--input__control_placeholder: #c5c5c5;--input__icon_search: #d5d5d5;--input__icon_clear: #000;--button-text: #9b9b9b;--button-hover: #707070;--button-active: #555;--mini-button-color: var(--input__icon_clear);--mini-button-hover: #888;--book-card__title: #303030;--book-card__title--hover: #bd1717;--book-card__author: #bbbbbb;--book-card__author--hover: #cfcfcf}:root[theme='moon']{--body: #282828;--scroll: #b6b6b6;--text: #999;--preloader: #9b6d00;--preloader-mini:#9b6d00;--header: #222;--theme-button-border: #444444;--theme-button-border-hover: #4e4e4e;--input__box: #2d2d2d;--input__box-border-top: var(--header);--input__box-border-bottom: var(--input__box);--input__box-border-focus: #aa7a00;--input__control: #888888;--input__control_color: #9c9c9c;--input__control_placeholder: #c5c5c5;--input__icon_search: #616161;--input__icon_clear: #fff;--button-text: #9b9b9b;--button-hover: #b6b6b6;--button-active: #aa7a00;--mini-button-color: var(--input__icon_clear);--mini-button-hover: #cc9208;--book-card__title: #bfbfbf;--book-card__title--hover: #bb8400;--book-card__author: #787878;--book-card__author--hover: #898989;--book-card__author: #898989;--book-card__author--hover: #787878}.scroll{height:calc(100vh - 73px);margin:0 3px;width:100vw;display:flex;justify-content:center;overflow:overlay;position:relative}@media screen and (max-width: 540px){.scroll{height:calc(100vh - 99px)}}

.book-card{padding:0px 0px;width:100px;display:flex;flex-direction:column;justify-content:end;grid-gap:5px;cursor:pointer}.book-card:hover .book-card__img{-webkit-filter:brightness(88%);filter:brightness(88%)}.book-card:hover .book-card__title{color:var(--book-card__title--hover)}.book-card:hover .book-card__author{color:var(--book-card__author--hover)}@media screen and (max-width: 364px){.book-card{width:35vw;height:auto}}@media screen and (max-width: 270px){.book-card{width:50vw;height:auto}}.book-card__img-container{height:145px;overflow:hidden;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:flex-end}@media screen and (max-width: 364px){.book-card__img-container{height:auto}}.book-card__img{pointer-events:none;height:auto;width:100%;border-radius:4px;overflow:auto}.book-card__description{pointer-events:none;width:100%;height:60px}.book-card__title{padding:0px 0px 0px 0px;margin:0px 0px 2px 0px;pointer-events:none;max-height:28px;overflow:hidden;font-size:12px;font-weight:400;word-break:break-all;color:var(--book-card__title)}.book-card__author{padding:0px 0px 0px 0px;margin:0px 0px 0px 0px;pointer-events:none;max-height:28px;overflow:hidden;font-size:12px;color:var(--book-card__author)}

.error{display:flex;flex-direction:column;flex-wrap:nowrap;align-items:center;color:var(--text)}.error__title{text-align:center;margin:10px;font-size:25px}.error__message{text-align:center;font-size:15px}

.theme{grid-area:theme;width:34px;opacity:0.55;cursor:pointer;overflow:hidden}.theme:hover{opacity:0.8}.theme__img{width:32px;height:32px;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.theme__icon{width:32px;height:32px;display:flex;justify-content:flex-start;transition-property:all;transition-duration:0.3s;transition-timing-function:linear}.theme__icon.sun{-webkit-transform:translate(0%);transform:translate(0%)}.theme__icon.moon{-webkit-transform:translate(-100%);transform:translate(-100%)}

.logo{grid-area:logo;height:28px;opacity:.8}.logo__img{height:100%;width:auto;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.logoText{grid-area:logoText;height:18px;opacity:.7}.logoText__img{height:auto;width:100%;max-width:200px;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center}

.zero-book{display:flex;flex-direction:column;flex-wrap:nowrap;align-items:center;color:var(--text)}.zero-book__title{text-align:center;margin:10px;font-size:25px}.zero-book__text{text-align:center;font-size:15px}

